import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { clone } from 'lodash';

import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';

import { PasswordPolicy, SettingService } from 'modules/organization/setting/setting.service';
import { OrganizationService } from 'modules/organization/organization.service';

const error_map = {
  'password_policy.minimum': 'Minimum password length',
  'password_policy.maximum': 'Maximum password length',
  'password_policy.expiry': 'Password expiration period',
  'password_policy.remember': 'Passwords to remember'
};

@Component({
  selector: 'app-manage-password-policy',
  templateUrl: './manage-password-policy.component.html',
  providers: [SettingService, OrganizationService],
  encapsulation: ViewEncapsulation.None
})
export class ManagePasswordPolicyComponent {
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('errorModal') errorModal: ErrorModalComponent;

  policy: PasswordPolicy = new PasswordPolicy();

  expiration: boolean = false;
  reuse: boolean = false;

  error: string = '';
  invalid_attributes: string[];

  constructor(
    private settingService: SettingService,
    private organizationService: OrganizationService
  ) {

    this.organizationService.getDetails().then(organization => {

      if (organization.password_policy) {

        this.policy = organization.password_policy;

        if (!this.policy.expiry) {
          this.policy.expiry = undefined;
        } else {
          this.expiration = true;
        }

        if (!this.policy.remember) {
          this.policy.remember = undefined;
        } else {
          this.reuse = true;
        }

      } else {

        this.policy.minimum = 8;
        this.policy.maximum = 16;
      }
    });
  }

  private __sanitizePolicy(policy) {

    if (!this.expiration) {
      policy.expiry = false;
    }
    if (!this.reuse) {
      policy.remember = false;
    }

    return policy;
  }

  savePolicy() {

    this.error = '';
    this.invalid_attributes = [];

    let policy = clone(this.policy);
    policy = this.__sanitizePolicy(policy);


    this.settingService.updatePasswordPolicy(policy)
      .then(response => {

        this.successModal.show();

      }).catch(err => {

        this.error = err.error.message.text;
        if (err.error.invalid_attributes) {
          for (const key in err.error.invalid_attributes) {
            if (err.error.invalid_attributes.hasOwnProperty(key)) {
              this.invalid_attributes.push(error_map[key] ? error_map[key] : err.error.invalid_attributes[key]);
            }
          }
        }
        this.errorModal.show();
      });
  }
}
