import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';

export class PasswordPolicy {

  minimum: number;
  maximum: number;
  lowercase: boolean;
  uppercase: boolean;
  numeric: boolean;
  special: boolean;
  expiry: number;
  remember: number;
  mfa: boolean;
}

@Injectable()
export class SettingService extends Api {

  private _base_url: string = '/organizations';
  private _feature_base_url: string = '/organizations/features';

  enableFeatures(feature_ids: any) {

    return this.put(this._feature_base_url, { 'features': feature_ids })
      .then(response => response);
  }

  disableFeatures(feature_ids: any) {

    return this.delete(this._feature_base_url, { 'features': feature_ids })
      .then(response => response);
  }

  updatePasswordPolicy(policy: PasswordPolicy) {

    return this.put(this._base_url + '/passwordpolicy', policy)
      .then(response => response);
  }
}
