import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';

import { SettingService } from 'modules/organization/setting/setting.service'

import { SuccessModalComponent  } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { AccessControlService, Module } from 'modules/access-control/access-control.service';
import { OrganizationService } from 'modules/organization/organization.service';
import { ModalDirective } from 'ngx-bootstrap';

class Feature {

  feature: string;
  feature_code: string;
  entity_ids: string[] = [];
  modules: Module[] = [];
  enabled: boolean = false;
}

enum Operation {
  ENABLE = 1, DISABLE
};

@Component({
  selector: 'app-manage-feature',
  templateUrl: './manage-feature.component.html',
  providers: [ SettingService, AccessControlService, OrganizationService ],
  encapsulation: ViewEncapsulation.None
})
export class ManageFeatureComponent implements OnInit {
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('errorModal') errorModal: ErrorModalComponent;
  @ViewChild('confirmModal') confirmModal: ModalDirective;

  featureList: Feature[];

  loading: Array<boolean>;
  message: string = '';

  feature: string;
  feature_code: string;
  operation: Operation = Operation.ENABLE;

  constructor(
    private settingService: SettingService,
    private accessControlService: AccessControlService,
    private organizationService: OrganizationService
  ) {

    this.featureList = new Array<Feature>();
    this.loading = [];
  }

  ngOnInit() {

    this.__loadEntities();
  }

  private __getIndex(list, value, key) {

    let index = -1;
    for (const i in list) {

      if (list[i][key] === value) {
        index = parseInt(i, 10);
        break;
      }
    }

    return index;
  }

  private __loadEntities() {

    this.accessControlService.getFeatureEntities()
      .then(response => {

        for (const feature of response) {

          const entity_ids = [];
          for (const module of feature.modules) {
            for (const entity of module.entities) {
              entity_ids.push(entity._id);
            }
          }

          const new_feature = new Feature();
          new_feature.feature = feature.feature;
          new_feature.feature_code = feature.feature_code;
          new_feature.entity_ids = entity_ids;
          new_feature.modules = feature.modules;
          this.loading[feature.feature_code] = false;
          this.featureList.push(new_feature);
        }

        this.__loadOrganization();

      }).catch(err => {

        this.message = err.error.message.text;
        this.errorModal.show();
      });
  }

  private __loadOrganization() {

    this.organizationService.getDetails()
      .then(response => {

        if (!response.features || !Array.isArray(response.features)) {
          return;
        }

        const organization_features = response.features;
        for (const feature of this.featureList) {
          for (const entity_id of feature.entity_ids) {

            if (organization_features.indexOf(entity_id) !== -1) {
              feature.enabled = true;
            }
          }
        }

      }).catch(err => {

        this.message = err.error.message.text;
        this.errorModal.show();
      });
  }

  operationConfirmation(operation, feature_code) {

    this.operation = operation;
    this.feature_code = feature_code;
    const index = this.__getIndex(this.featureList, feature_code, 'feature_code');
    this.feature = this.featureList[index].feature;
    this.confirmModal.show();
  }

  disableFeature() {

    const self = this;
    this.confirmModal.hide();

    this.loading[this.feature_code] = true;
    const index = this.__getIndex(this.featureList, this.feature_code, 'feature_code');
    this.settingService.disableFeatures(this.featureList[index].entity_ids)
      .then(response => {

        this.loading[self.feature_code] = false;
        this.featureList[index].enabled = false;
        this.message = response.message.text;
        this.successModal.show(function () {
          window.location.reload();
        });

      }).catch(err => {

        this.loading[self.feature_code] = false;
        this.message = err.error.message.text;
        this.errorModal.show();
      });
  }

  enableFeature() {

    const self = this;
    this.confirmModal.hide();

    this.loading[this.feature_code] = true;
    const index = this.__getIndex(this.featureList, this.feature_code, 'feature_code');
    this.settingService.enableFeatures(this.featureList[index].entity_ids)
      .then(response => {

        this.loading[self.feature_code] = false;
        this.featureList[index].enabled = true;
        this.message = response.message.text;
        this.successModal.show(function () {
          window.location.reload();
        });

      }).catch(err => {

        this.loading[self.feature_code] = false;
        this.message = err.error.message.text;
        this.errorModal.show();
      });
  }
}
