import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthenticationGuard } from 'core/services/authentication.service';
import { AuthorizationGuard } from 'core/services/authorization.service';
import { ManageFeatureComponent } from './manage-feature/manage-feature.component';
import { ManagePasswordPolicyComponent } from './manage-password-policy/manage-password-policy.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Setting'
    },
    children: [
      {
        path: '',
        redirectTo: 'dashboard'
      },
      {
        path: 'feature',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'Feature',
          access: {
            module: 'organization',
            entity: 'feature',
            action: 'enable'
          }
        },
        component: ManageFeatureComponent
      },
      {
        path: 'password-policy',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'Password Policy',
          access: {
            module: 'organization',
            entity: 'organization',
            action: 'passwordpolicy'
          }
        },
        component: ManagePasswordPolicyComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SettingRoutingModule { }
